@include tablet {
    .contenedor-anuncios{
    @include grid (3, 2rem);
    }
}

.anuncio{
    background-color: lighten($gris, 5);
    border: 1px solid darken($gris, 10);
    margin-bottom: 2rem;
    .anuncio-imagen{
        height: 25rem;

        img{
           width: 385px;
           height: 216px;
           background-image: cover;
        }
    }
    .contenido-anuncio{
        padding: 2rem;
    }
    h3, p{
        margin: 0;
    }

}
.precio{
    color: $verde;
    font-weight: bold;
    font-size: 3rem;
}

.iconos-caracteristicas{
    @include desktop {
        display: flex;
    }
    list-style: none;
    padding: 0;
    max-width: 40rem;

    li{
        flex: 1;
        display: flex;

        img{
            flex: 0 0 3rem;
            margin-right: 1.5rem;
        }
        p{
            font-weight: bold;
        }
    }

}
