@include tablet {
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog{
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
        margin-bottom: 2rem;
    }
    &:last-of-type{
        margin-bottom: 0;
    }
    p{
        margin: .5rem;
        font-size: 1.8rem;
    }
}

.texto-entrada{
    a{
        color: $negro;
        margin-top: 2rem;
        display: block;

        @include tablet {
            margin-top: 0;
        }
    }
    h4{
        margin: 0;
        line-height: 1.2;

        &::after{
           content: "";
           display: block;
           width: 15rem;
           height: .5rem;
           background-color: $verde;
           margin-top: 1rem; 
        }
    }
}

.informacion-meta{
    span{
        color: $amarillo;
    }
}